import uniLoadMore from '@/components/uni-load-more.vue';
import dividLine from '@/components/line.vue';
import goodList from '@/components/good-list.vue';
import goodRow from '@/components/good-list-row.vue';
import goTop from '@/components/go-top.vue'
import {
	getGoodsList,
	getCTK,
	getOtherGood,
	getItemsDes,
	getS11
} from '@/api/goods.js'
export default {
	props: {
		productgood: {
			type: Object
		},
		current: {
			type: [Number, String],
			default: 0
		},
		type: {
			type: String,
		},
		isgood: {
			type: Boolean,
			default: false
		},
		isSE:{
			type:Boolean,
			default:false
		},
		SEGoods:Array
	},
	data() {
		return {
			imgUrls: [
				'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg',
				'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg'
			],
			scrolltop: 0,
			page: 1,
			scrollEv: null,
			nowScroll: 0,
			isScroll: false, 
			isAndroid: false,
			timer: null,
			isShow: false,
			globScrollTop: 0,
			uni: "",
			swiper: "",
			tab: "",
			active: 0,
			isup: 3,
			sorts: new Object(),
			isDefault: true, //默认排序
			isTotal_sales: 0, //销量排序
			isPrice: 0, //价格排序
			count: 0,
			screen: null,
			isfixed: false,
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			duration: 300,
			loadingText: {
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			type: "",
			rcProductgood: new Array()
		}
	},
	components: {
		uniLoadMore,
		dividLine,
		goodList,
		goTop,
		goodRow
	},
	computed: {
		up() {
			let color = this.isup == 0 ? '#ff0000' : '#ccc'
			return color
		},
		down() {
			let color = this.isup == 1 ? '#ff0000' : '#ccc';
			return color;
		}
	},

	created() {
		console.log('!!',);
		console.log(JSON.stringify(this.productgood)); 
		console.log(this.isSE);
		this.getRecommendGood();
	}, 
	onLoad() {
		
	},
	methods: {
		//条件筛选
		screentap(sortType) {
			if (sortType == '') {
				this.isDefault = true;
				this.isTotal_sales = 0;
				this.isPrice = 0;
				this._getGoodsList({
					sorts: sortType
				});
			} else if (sortType == 'total_sales') {
				this.isDefault = false;
				this.isPrice = 0;
				if (this.isTotal_sales == 0 || this.isTotal_sales == 1) {
					this._getGoodsList({
						sorts: sortType + '_des'
					});
					this.isTotal_sales = 2;
				} else if (this.isTotal_sales == 2) {
					this.sorts.sort1 = sortType + '_asc';
					this._getGoodsList({
						sorts: sortType + '_asc'
					});
					this.isTotal_sales = 1;
				}
			} else if (sortType == 'price') {
				this.isDefault = false;
				this.isTotal_sales = 0;
				if (this.isPrice == 0 || this.isPrice == 1) {
					this._getGoodsList({
						sorts: sortType + '_des'
					});
					this.isPrice = 2;
				} else {
					this._getGoodsList({
						sorts: sortType + '_asc'
					});
					this.isPrice = 1;
				}
			}
		},
		getRecommendGood() {
			this.rcProductgood = uni.getStorageSync("rcProductgood");
			getOtherGood({
				page: this.page,
				type: "item"
			}).then(res => {
				//console.log(JSON.stringify(res.result.data));
				this.rcProductgood=res.result.data;
				uni.setStorageSync("rcProductgood", this.rcProductgood);
			});
		},
		_getGoodsList(obj) {
			console.log(obj);
			uni.showLoading({
				title: '加载中..'
			})
			let ret = getGoodsList({
				sorts:obj.sorts
			});
			// let ret=getCTK({
			// 	page
			// })
			ret.then(res => {
				console.log(res);
				uni.hideLoading();
				if (res.code == 200) {
					this.productgood.product = res.result
					// this.productgood.product = res.content
					for (var i = 0; i < this.productgood.product.length; i++) {
						//console.log(JSON.stringify(this.productgood.product[i]))
					}
				} else {
					this._showError()
				}
			})
		},
		//条件筛选滑动事件监听 (this._delayfun)
		_screensticky(ev) {
			// let target = ev.target || ev.srcElement;
			// // this.nowScroll = target.scrollTop
			// this.nowScroll = target.scrollTop
			// // console.log('滚动', this.nowScroll);
			// if (this.nowScroll > 790) {
			// 	if (this.isAndroid) {
			// 		this.isfixed = true;
			// 	}
			// 	this.isfixed = true;
			// 	this.isShow = true;
			// } else {
			// 	this.isfixed = false;
			// 	this.isShow = false;
			// }
		},
		jump(type) {
			if (type == 'search') {
				uni.navigateTo({
					url: '/pages/common/search'
				})
				return
			}
			let table = type;
			const map = new Map()
				.set('jhs', '聚划算')
				.set('special_offer', '9.9包邮')
				.set('tqg_goods', '淘抢购')
				.set('brandvoucher', '品牌好券')
				.set('tmjx', '精选商品')
			if (type == 'jhs' || type == 'tj' || type == 'tmjx') {
				table = 'yhq_goods'
			}
			uni.navigateTo({
				url: `/pages/common/good-item?type=${table}&title=${map.get(type)}`
				// url: '/pages/common/good'
			});
		},
		goTop() {
			this.globScrollTop = this.nowScroll
			this.$nextTick(function() {
				this.globScrollTop = 0;
				this.isfixed = false;
				this.isShow = false;
				console.log(this.isfixed, this.isShow);
			})
		},
		godetail(id) {
			if (!this.isgood) {
				var page = this.page;
				uni.navigateTo({
					url: `/pages/common/goods-detail?id=${id}&page=${page}`
				})
			} else {
				uni.navigateTo({
					url: `/pages/common/goods2-detail?id=${id}`
				})
			}
			this.globScrollTop = this.nowScroll;
		},
		//触底加载更多
		onloadmore() {
			if (this.loadingType !== 0) {
				return;
			}
			this.loadingType = 1;
			let ret = getGoodsList({
				page: this.page,
				// screen: '',
				// jg: '',
				type: this.type,
			});
			ret.then(res => {
				console.log(res);
				if (res.code == 200) {
					if (res.result.length == 0) {
						this.loadingType = 3;
						return
					}
					// this.productgood.product = this.productgood.product.concat(res.result);
					this.productgood.product = res.result;
					this.page++; 
					this.loadingType = 0;
				} else {
					this._showError()
				}
			});

			// let ret1 = getOtherGood({
			// 	page: this.page,
			// 	type: "item"
			// });
			// ret1.then(res => {
			// 	console.log(res);
			// 	if (res.code == 200) {
			// 		this.rcProductgood = this.rcProductgood.concat(res.result);
			// 		this.page++;
			// 		this.loadingType = 0;
			// 	} else {
			// 		this._showError()
			// 	}
			// })
		},
	}
}
